"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { ChatMessage } from "@/components/ChatMessage"
import { ArrowLeft, Send } from "lucide-react"
import Link from "next/link"

interface Message {
  id: string
  content: string
  sender: "user" | "match"
  timestamp: string
}

interface Chat {
  id: string
  matchName: string
  image: string
  lastMessage: string
  messages: Message[]
}

// Sample chat data - in a real app, this would come from an API
const sampleChats: Chat[] = [
  {
    id: "1",
    matchName: "Sophie",
    image: "https://images.pexels.com/photos/1858175/pexels-photo-1858175.jpeg",
    lastMessage: "Salut ! Comment vas-tu ?",
    messages: [
      {
        id: "1",
        content: "Salut ! Comment vas-tu ?",
        sender: "match",
        timestamp: "2024-01-20T10:00:00",
      },
      {
        id: "2",
        content: "Très bien, merci ! Et toi ?",
        sender: "user",
        timestamp: "2024-01-20T10:01:00",
      },
    ],
  },
  {
    id: "2",
    matchName: "Emma",
    image: "https://images.pexels.com/photos/1382731/pexels-photo-1382731.jpeg",
    lastMessage: "On se voit demain ?",
    messages: [
      {
        id: "1",
        content: "Hey! Tu aimes le café ?",
        sender: "match",
        timestamp: "2024-01-20T09:00:00",
      },
      {
        id: "2",
        content: "Oui, j'adore ! Pourquoi ?",
        sender: "user",
        timestamp: "2024-01-20T09:01:00",
      },
      {
        id: "3",
        content: "On se voit demain ?",
        sender: "match",
        timestamp: "2024-01-20T09:02:00",
      },
    ],
  },
]

export default function ChatPage() {
  const [selectedChat, setSelectedChat] = useState<Chat | null>(null)
  const [newMessage, setNewMessage] = useState("")
  const [chats, setChats] = useState<Chat[]>(sampleChats)

  const handleSendMessage = (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!newMessage.trim() || !selectedChat) return

    const message: Message = {
      id: Date.now().toString(),
      content: newMessage,
      sender: "user",
      timestamp: new Date().toISOString(),
    }

    setChats(prevChats =>
      prevChats.map(chat =>
        chat.id === selectedChat.id
          ? {
              ...chat,
              lastMessage: newMessage,
              messages: [...chat.messages, message],
            }
          : chat
      )
    )

    setNewMessage("")
  }

  return (
    <div className="flex h-screen bg-black">
      {/* Chat list sidebar */}
      <div className={`w-full border-r border-zinc-800 bg-zinc-900 md:w-80 ${
        selectedChat ? "hidden md:block" : "block"
      }`}>
        <div className="p-4">
          <h1 className="mb-4 text-xl font-bold text-white">Messages</h1>
          <div className="space-y-2">
            {chats.map((chat) => (
              <button
                key={chat.id}
                className="flex w-full items-center space-x-3 rounded-lg p-3 text-left hover:bg-zinc-800"
                onClick={() => setSelectedChat(chat)}
              >
                <img
                  src={chat.image}
                  alt={chat.matchName}
                  className="h-12 w-12 rounded-full object-cover"
                />
                <div className="flex-1 overflow-hidden">
                  <h3 className="font-semibold text-white">{chat.matchName}</h3>
                  <p className="truncate text-sm text-zinc-400">
                    {chat.lastMessage}
                  </p>
                </div>
              </button>
            ))}
          </div>
        </div>
      </div>

      {/* Chat window */}
      <div className={`flex h-full w-full flex-col bg-zinc-900 ${
        !selectedChat ? "hidden md:flex" : "flex"
      }`}>
        {selectedChat ? (
          <>
            {/* Chat header */}
            <div className="flex items-center border-b border-zinc-800 p-4">
              <Button
                variant="ghost"
                size="icon"
                className="mr-2 md:hidden"
                onClick={() => setSelectedChat(null)}
              >
                <ArrowLeft className="h-6 w-6 text-white" />
              </Button>
              <img
                src={selectedChat.image}
                alt={selectedChat.matchName}
                className="h-10 w-10 rounded-full object-cover"
              />
              <h2 className="ml-3 text-lg font-semibold text-white">
                {selectedChat.matchName}
              </h2>
            </div>

            {/* Messages */}
            <div className="flex-1 overflow-y-auto p-4">
              {selectedChat.messages.map((message) => (
                <ChatMessage key={message.id} message={message} />
              ))}
            </div>

            {/* Message input */}
            <form onSubmit={handleSendMessage} className="border-t border-zinc-800 p-4">
              <div className="flex space-x-2">
                <Input
                  value={newMessage}
                  onChange={(e) => setNewMessage(e.target.value)}
                  placeholder="Écrivez votre message..."
                  className="flex-1 bg-zinc-800 text-white"
                />
                <Button type="submit" disabled={!newMessage.trim()}>
                  <Send className="h-5 w-5" />
                </Button>
              </div>
            </form>
          </>
        ) : (
          <div className="flex flex-1 items-center justify-center">
            <p className="text-center text-zinc-500">
              Sélectionnez une conversation pour commencer à chatter
            </p>
          </div>
        )}
      </div>
    </div>
  )
}
